@use '../../../shared/styles/variable/globalIndex';
@use '../../../shared/styles/variable/global';
@use '../../../shared/styles/mixins';
@use './../../../shared/styles/variable/common';


.services {
  display: flex;
  //background: #2F164B;
  width: mixins.rem(250);
  align-items: center;
  justify-content: center;
  border-radius: mixins.rem(15);
  column-gap: mixins.rem(20);
  cursor: pointer;
  background: radial-gradient(circle, #D21C72, #BC188D, #600F5F, #2F164B);

}

.wrapper_main {
  display: flex;
  flex-direction: column;
  row-gap: mixins.rem(45);
  padding-bottom: mixins.rem(60);
}

.title_iq_pump {
  font-family: "Luckiest Guy";
  font-weight: 700;
  color: #91FCA5;
  font-size: mixins.rem(24);
}

.cover_sub_title {
  display: flex;
  justify-content: center;
  min-width: 100%;
  text-align: center;
}

.title_main {
  @extend .titleBlock;
  max-width: mixins.rem(537);
  @include mixins.adaptiveValue('font-size', 24, 16, 3, 800, 440);
}

.svg_brain_block{
  width: mixins.rem(65);
  height: mixins.rem(100);
  border-radius: 50%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  background: radial-gradient(circle farthest-side at 50% 17%,
          #D21C72 0%,
          #BC188D 3%,
          #600F5F 17%,
          #2F164B 25%
  );
  border-radius: mixins.rem(15);
  align-items: center;
  padding-top: mixins.rem(30);
  padding-bottom: mixins.rem(40);
  padding-left: mixins.rem(30);
  padding-right: mixins.rem(30);
  row-gap: mixins.rem(60);
  box-sizing: border-box;

  @media (min-width: mixins.em(global.$mobile)) {
    height: 98%;
    max-width: 100%;
    position: relative;
    width: auto;
    aspect-ratio: 9 / 16;
  }

  @media (max-width: mixins.em(global.$mobile)) {
    position: relative;
    width: 98%;
    height: 100%;
  }

  @media (max-width: mixins.em(450)) {
    min-height: 98vh;
  }

}

.title_cover {
  display: flex;
  flex-direction: column;
  align-items: center;
  color:#FFFFFF;
  font-family: 'Lato';
  font-weight: 600;
  font-size: mixins.rem(16);
  line-height: globalIndex.$line_height;
  text-align: center;
  row-gap: mixins.rem(20);
  border-radius: 50%;
  width: mixins.rem(190);
  height: mixins.rem(190);
  display: flex;
  justify-content: center;
  align-items: center;
}

.title {
  font-family: "Luckiest Guy";
  font-weight: 700;
  color: #91FCA5;
}

.svg_brain {
  width: mixins.rem(85);
  height: mixins.rem(100);
  border-radius: 50%;
}

.th_info {

}

.user_name_tg {

}

.adress {

}

.balance_block{
  background-color: rgba(255, 255, 255, 0.2);
  display: flex;
  flex-direction: column;
  min-width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: mixins.rem(15);
  row-gap: mixins.rem(20);
  padding-top: mixins.rem(20);
  padding-bottom: mixins.rem(20);
  padding-left: mixins.rem(10);
  padding-right: mixins.rem(10);
}

.subtitle_block {
  font-size: mixins.rem(16);
  font-weight: 400;
  font-family: 'Lato';
  color: #FFFFFF;
}

.balance_stt {
  font-family: "Bebas Neue";
  font-size: mixins.rem(36);
  font-weight: 700;
  color: #39E476;
}

.btn_cash{
  background-color: #442D5D;
  padding-bottom: mixins.rem(20);
  padding-top: mixins.rem(20);
  transition: background-color ease 0.3s 0s, color ease 0.3s 0s;
  min-width: mixins.rem(120);

  @media (any-hover: hover) {
    &:hover {
      transition: background-color ease 0.3s 0s, color ease 0.3s 0s;
      background-color: #39E476;
      color: black;
    }
  }

  &.left {
    padding-left: mixins.rem(30);
    border-top-left-radius: mixins.rem(40);
    border-bottom-left-radius: mixins.rem(40);
    padding-right: mixins.rem(10);
    border-right: mixins.rem(1) solid rgba(255, 255, 255, 0.3);
  }
  &.right {
    border-left: mixins.rem(1) solid rgba(255, 255, 255, 0.3);
    border-top-right-radius: mixins.rem(40);
    border-bottom-right-radius: mixins.rem(40);
    padding-right: mixins.rem(30);
    padding-left: mixins.rem(10);
  }
}

.btns_block {
  font-size: mixins.rem(14);
  font-weight: 400;
  font-family: 'Lato';
  color: #FFFFFF;
  display: flex;


  &:first-child {

  }

  &:last-child {


  }

}

