.input_block {
  display: flex;
  flex-direction: column;
  row-gap: mixins.rem(10);
  min-width: 100%;
}

.wrap_inp {

  min-width: 100%;
  width: 100%;
  flex: 1 1 auto;
  height: mixins.rem(48);
  font-size: mixins.rem(16);
  font-weight: 600;
  line-height: globalIndex.$line_height;
  position: relative;

}

.inp {
  color: #000000;
  font-size: mixins.rem(16);
  height: 100%;
  width: 100%;
  border-radius: mixins.rem(15);
  background-color: #FFFFFF;
  padding-top: mixins.rem(5);
  padding-bottom: mixins.rem(5);
  font-weight: 700;
  font-family: 'Lato';
  min-width: 100%;
  @media (min-width: mixins.em(380)) {
    padding-left: mixins.rem(15);
    padding-right: mixins.rem(15);
  }

  @media (max-width: mixins.em(380)) {
    padding-left: mixins.rem(15);
    padding-right: mixins.rem(64);
  }

  &::placeholder {
    font-family: 'Lato';
    font-weight: 700;
  }

}

.range {
  display: flex;
  flex-direction: column;
  align-items: center;
  color:#FFFFFF;
  font-family: 'Lato';
  font-weight: 400;
  font-size: mixins.rem(16);
  text-align: center;
  row-gap: mixins.rem(20);
}

.btn_send {
  background-color: #39E476;
  color: #FFFFFF;
  border: none;
  cursor: pointer;
  box-shadow: mixins.rem(0) mixins.rem(0) mixins.rem(10) rgba(0, 0, 0, 0.2);
  transition: background-color ease 0.3s, box-shadow ease 0.3s;
  padding-left: mixins.rem(28);
  padding-right: mixins.rem(28);
  min-height: mixins.rem(70);
  padding-top: mixins.rem(18);
  padding-bottom: mixins.rem(18);
  font-size: mixins.rem(16);
  font-weight: 700;
  font-family: globalIndex.$font_family_btn_first;
  border-radius: mixins.rem(40);
  min-width: mixins.rem(137);

  @media(any-hover: hover) {
    &:hover {
      background-color: darken(globalIndex.$btn_background_hover, 10%);
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
      transition: background-color ease 0.3s, box-shadow ease 0.3s;
    }
  }
}

.btn_cancel {
  //background-color: #39E476;
  color: #FFFFFF;
  border: none;
  cursor: pointer;
  //box-shadow: mixins.rem(0) mixins.rem(0) mixins.rem(10) rgba(0, 0, 0, 0.2);
  transition: background-color ease 0.3s, box-shadow ease 0.3s;
  padding-left: mixins.rem(28);
  padding-right: mixins.rem(28);
  min-height: mixins.rem(70);
  padding-top: mixins.rem(18);
  padding-bottom: mixins.rem(18);
  font-size: mixins.rem(16);
  font-weight: 700;
  font-family: globalIndex.$font_family_btn_first;
  border-radius: mixins.rem(40);
  min-width: mixins.rem(137);

  @media(any-hover: hover) {
    &:hover {
      background-color: darken(globalIndex.$btn_background_hover, 10%);
      //box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
      transition: background-color ease 0.3s, box-shadow ease 0.3s;
    }
  }
}