@use '../../shared/styles/variable/globalIndex';
@use '../../shared/styles/variable/global';
@use '../../shared/styles/mixins';
@use '../../shared/styles/variable/common';

.leaflet-container {
  z-index: 0; /* Установите z-index, чтобы карта не перекрывала другие элементы */
  position: relative;
}

.cover_map {

  width: 100%;
  z-index: 0;
  border-radius: mixins.rem(30);

  @media (min-width: mixins.em(global.$mobile)) {
    height: mixins.rem(421);
  }
  @media (max-width: mixins.em(global.$mobile)) {
    height: mixins.rem(440);
  }
}
.markercluster_count {
  color: #FFFFFF;
  display: block;
}

.black_circle_marker {
  background-color: black;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  border: 2px solid white;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
}

.markercluster {
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  color: #FFFFFF;
  border: 2px solid white;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
}