@use '../../shared/styles/variable/globalIndex';
@use '../../shared/styles/variable/global';
@use '../../shared/styles/mixins';
@use '../../shared/styles/variable/common';

.wrapper {
    border-radius: mixins.rem(15);
    display: flex;
    flex-direction: column;
    position: relative;
    padding-top: 0;
    row-gap: mixins.rem(75);
    background-color: #AFFCD0;
    max-height: 95vh;
    overflow-y: auto;
    padding-bottom: mixins.rem(42);
    @include mixins.adaptiveValue('width', 1197, 980, 3, 1250, 1000);
    @include mixins.adaptiveValue('padding-right', 42, 37, 3, 1250, 1000);
    @include mixins.adaptiveValue('padding-left', 42, 37, 3, 1250, 1000);
    @include mixins.adaptiveValue('row-gap', 75, 30, 3, 1250, 1000);

    @include mixins.adaptiveValue('padding-right', 37, 7, 3, 600, 320);
    @include mixins.adaptiveValue('padding-left', 37, 7, 3, 600, 320);

    @media (min-width: global.$mobile) {
        padding-right: mixins.rem(42);
        padding-left: mixins.rem(42);
        padding-bottom: mixins.rem(42);
    }

    @media (max-width: global.$mobile) {
        padding-right: mixins.rem(0);
        padding-left: mixins.rem(22);
        padding-bottom: mixins.rem(42);
    }


    @media (min-width: mixins.em(1251)) {
        width: mixins.rem(1197);
    }

    @media (max-width: mixins.em(1250)) {
        min-width: 100%;
        max-width: 85%;
    }

    @media (max-width: mixins.em(1250)) {
        min-width: 100%;
        max-width: 98vw;
    }
}

.coverTitle {
    display: flex;
    min-width: 100%;
    justify-content: center;
    text-transform: uppercase;
    font-size: mixins.rem(24);
    font-weight: 600;
    font-family: globalIndex.$font_family_main;
    align-items: center;
    min-height: mixins.rem(68);

}

.title {
    color: globalIndex.$btn_color;
    font-family: globalIndex.$font_family_main;
}

.coverBlock {
    background: #C4FFE3;
    height: 100%;
    display: flex;
    justify-content: center;
    width: mixins.rem(372);
    border-radius: 0 0 mixins.rem(20) mixins.rem(20);
    align-items: center;
}

.bodyBlock {
    display: flex;
    column-gap: mixins.rem(80);
    row-gap: mixins.rem(10);
    @include mixins.adaptiveValue('column-gap', 80, 25, 3, 1250, global.$tablet);

    @media (min-width: mixins.em(691)) {
        //align-items: center;
        justify-content: center;
    }

    @media (max-width: mixins.em(690)) {
        flex-direction: column;
        justify-content: center;
    }
}

.left_block {
    display: flex;
    flex-direction: column;
    flex: 1 0 mixins.rem(300);
    max-width: mixins.rem(300);
    align-items: center;

    @media (max-width: mixins.em(global.$mobile)) {
        row-gap: mixins.rem(10);
    }
    &.desctop {
        @media (max-width: mixins.em(global.$tablet)) {
            display: none;
            visibility: hidden;
            opacity: 0;
        }
    }
}

.center_block_mobile {
    @media (min-width: mixins.em(global.$tablet)) {
        display: none;
        visibility: hidden;
        opacity: 0;
    }

    @media (max-width: mixins.em(global.$tablet)) {
      min-height: 100%;
      width: mixins.rem(126);
      text-align: center;
        display: flex;
        flex-direction: column;
      row-gap: mixins.rem(6);

    }
}

.centered_block {
    flex: 1 0  mixins.rem(300);
    max-width: mixins.rem(300);
    display: flex;
    flex-direction: column;
    row-gap: mixins.rem(16);

    @media (max-width: mixins.em(690)) {
        min-width: 100%;
    }
}

.text_center {
    display: flex;
    flex-direction: column;
    row-gap: mixins.rem(5);
    justify-content: center;
    text-align: center;
}

.coverBtns {
    display: flex;
    column-gap: mixins.rem(15);
    align-items: center;
    width: 100%;
    justify-content: center;
    font-family: globalIndex.$font_family_main;
    flex: 1 1 auto;
    row-gap: mixins.rem(8);

    @media (min-width: mixins.em(global.$tablet)) {
        flex-direction: column;
    }

}

.max {
    font-family: globalIndex.$font_family_title;
    font-size: mixins.rem(20);
    font-weight: 700;
    line-height: globalIndex.$line_height;
    color: #7C7C7C;
}

.coverBtns {
    display: flex;
    column-gap: mixins.rem(15);
    align-items: center;
    width: 100%;
    justify-content: center;
    transition: fill ease 0.3s 0s;
}

.right_block {
    flex: 1 0  mixins.rem(300);
    max-width: mixins.rem(300);
    display: flex;
    flex-direction: column;
    row-gap: mixins.rem(16);
    align-items: flex-start;

    @media (max-width: mixins.em(690)) {
        min-width: 100%;
    }
}

.svg_logo {
    background: transparent;
    width: mixins.rem(42);
    height: mixins.rem(35);
    fill: globalIndex.$color_main_second;
    stroke-width: 1;
    transition: fill ease 0.3s 0s;
    @media (any-hover: hover) {
        &:hover {
            fill: globalIndex.$color_title;
            transition: fill ease 0.3s 0s;
        }
    }
}

.svgBanner {
    background: transparent;
    width: mixins.rem(37);
    height: mixins.rem(37);
    fill: globalIndex.$color_main_second;
    transition: fill ease 0.3s 0s;
    @media (any-hover: hover) {
        &:hover {
            fill: globalIndex.$color_title;
            transition: fill ease 0.3s 0s;
        }
    }
}

.svgLogoBanner {
    width: mixins.rem(40);
    height: mixins.rem(50);
}

.aboutYou {
    display: flex;
    flex-direction: column;
    row-gap: mixins.rem(16);
    flex: 1 1 auto;
    min-width: 100%;
    justify-content: center;

}

.coverValue {
    padding-top: mixins.rem(40);
    min-width: 100%;
    display: flex;
    column-gap: mixins.rem(10);
    justify-content: space-between;
    align-items: center;

    color: #409798;
    line-height: globalIndex.$line_height;
}

.mlm_text {
    font-family: globalIndex.$font_family_main;
    font-weight: 700;
    font-size: mixins.rem(20);
}

.mlm_percent {
    font-family: globalIndex.$font_family_third;
    font-weight: 700;
    font-size: mixins.rem(20);
}


.coverTabs {
    display: flex;
    column-gap: mixins.rem(10);
    min-width: 100%;
    justify-content: center;

    @media (min-width: mixins.em(420)) {
        max-width: 80%;
    }
    @media (max-width: mixins.em(420)) {
    }
}

.block_mlm {
    display: flex;
    width: mixins.rem(42);
    height: mixins.rem(42);
    color: #409798;
    background-color: rgba(255, 255, 255, 0.4);
    font-family: globalIndex.$font_family_third;
    font-size: mixins.rem(14);
    font-weight: 700;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: mixins.rem(10);

    &.active {
        background-color: #73E4BF;
        color: #FFFFFF;
    }
}

.textBtnAdd {
    display: flex;
    flex-direction: column;
}

.lastSpan {
    font-size: mixins.rem(14);
    font-weight: 400;
    font-family: globalIndex.$font_family_third;
    line-height: globalIndex.$line_height;
}

.firstSpan {
    font-size: mixins.rem(16);
    font-weight: 600;
    font-family: globalIndex.$font_family_main;
    line-height: globalIndex.$line_height;
}

.coverIncognito {
    padding-top: mixins.rem(20);
    padding-bottom: mixins.rem(20);
    justify-content: flex-start;
}

.coverAddBtn {
    display: flex;
    min-width: 100%;
    justify-content: center;
}


.cover_btn_banner {
    width: 100%;
    height: 100%;
    cursor: pointer;
    background: rgba(255, 255, 255, 0.4);
    border: mixins.rem(3) transparent;
    border-radius: mixins.rem(15);
    transition:
            background ease 0.3s 0s,
            box-shadow ease 0.3s 0s;

    .loaded & {
        border: mixins.rem(3) solid globalIndex.$color_title;
        background: #8ff3e6;
    }
    .errorLoaded & {
        background-color: #ddc9c7;
    }

    @media (any-hover: hover) {
        &:hover {
            background: rgba(195, 192, 192, 0.59);
            transition:
                    background ease 0.3s 0s,
                    box-shadow ease 0.3s 0s;
            box-shadow: mixins.rem(0) mixins.rem(0) mixins.rem(10) mixins.rem(3) rgba(0, 0, 0, 0.15);
        }
    }

    @media (max-width: mixins.em(global.$tablet)) {
        min-height: mixins.rem(126);
        display: flex;
    }
}

.cover_btn_logo {
    width: mixins.rem(126);
    height: mixins.rem(126);
    background: rgba(255, 255, 255, 0.4);
    border: mixins.rem(3) transparent;
    border-radius: 50%;
    transition:
            background ease 0.3s 0s,
            box-shadow ease 0.3s 0s;

    .loaded & {
        border: mixins.rem(3) solid globalIndex.$color_title;
        background: #8ff3e6;
    }
    .errorLoaded & {
        background-color: #ddc9c7;
    }
    @media (any-hover: hover) {
        &:hover {
            background: rgba(195, 192, 192, 0.59);
            transition:
                    background ease 0.3s 0s,
                    box-shadow ease 0.3s 0s;
            box-shadow: mixins.rem(0) mixins.rem(0) mixins.rem(10) mixins.rem(3) rgba(0, 0, 0, 0.15);
        }
    }

}

.cover_btn_banner_text {
    display: flex;
    flex-direction: column;
    row-gap: mixins.rem(5);
    justify-content: center;
    align-items: center;
}

.text_info {
    font-family: globalIndex.$font_family_main;
    font-weight: 700;
    line-height: globalIndex.$line_height;
    font-size: mixins.rem(20);
    color: #7C7C7C;
}

.text_info_logo {
    font-family: globalIndex.$font_family_title;
    font-weight: 700;
    line-height: globalIndex.$line_height;
    font-size: mixins.rem(20);
    color: #7C7C7C;
}


.maxSize {
    text-align: center;
    font-size: mixins.rem(20);
    font-weight: 700;
    font-family: globalIndex.$font_family_third;
    line-height: globalIndex.$line_height;
    color: #7C7C7C;
}
